.x-radio {
  display: inline-flex;
  align-items: center;
  padding: 0.1rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.x-radio:active {
  opacity: 1;
}

.x-radio:active .x-radio__icon {
  background: #e6e6e6;
}

.x-radio__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 0.32rem;
  height: 0.32rem;
  border-radius: 50%;
  background: #f0f0f0;
  overflow: hidden;
  -webkit-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
}

.x-radio__icon svg {
  display: block;
  width: 0.2rem;
  height: 0.2rem;
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}

.x-radio__icon path {
  fill: #fff;
}

.x-radio__text {
  display: block;
  font-size: 0.26rem;
  color: #888;
  margin-left: 0.1rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.x-radio__checked .x-radio__icon {
  background: #00bb55;
}

.x-radio__checked .x-radio__icon svg {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

.x-radio__checked:active .x-radio__icon {
  background: #00ac4e;
}

.x-radio__checked .x-radio__text {
  color: #333;
}

.x-radio__disabled:active {
  opacity: 1 !important;
}

.x-radio__disabled.x-radio__checked .x-radio__icon {
  opacity: 0.7;
}

.x-radio__disabled .x-radio__text {
  color: #bbb;
}