.x-popup {
  align-items: flex-end !important;
}

.x-popup--top {
  align-items: flex-start !important;
}

.x-popup__inner {
  overflow: auto;
  padding: 0.4rem;
  flex: 1 1;
}

.x-popup--no-padding {
  padding: 0;
}

.x-popup__scroller {
  flex: 1 1;
  display: flex;
}

.x-popup__inscroller {
  flex: 1 1;
  margin: 0 -0.4rem 0 0;
  padding: 0 0.4rem 0 0;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.x-popup--no-scroll {
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.x-popup .x-modal__inner {
  height: 50%;
  display: flex;
  flex-direction: column;
  background: #fff;
}

.x-popup.x-modal--enter .x-modal__inner {
  -webkit-animation: __x_popup_slideup_enter cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
  animation: __x_popup_slideup_enter cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
}

.x-popup.x-modal--enter.x-popup--top .x-modal__inner {
  -webkit-animation: __x_popup_slidedown_enter cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
  animation: __x_popup_slidedown_enter cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
}

.x-popup.x-modal--leave .x-modal__inner {
  -webkit-animation: __x_popup_slideup_leave cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
  animation: __x_popup_slideup_leave cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
}

.x-popup.x-modal--leave.x-popup--top .x-modal__inner {
  -webkit-animation: __x_popup_slidedown_leave cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
  animation: __x_popup_slidedown_leave cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
}

@-webkit-keyframes __x_popup_slideup_enter {
  0% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }

  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes __x_popup_slideup_enter {
  0% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }

  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@-webkit-keyframes __x_popup_slideup_leave {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  100% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }
}

@keyframes __x_popup_slideup_leave {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  100% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }
}

@-webkit-keyframes __x_popup_slidedown_enter {
  0% {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }

  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes __x_popup_slidedown_enter {
  0% {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }

  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@-webkit-keyframes __x_popup_slidedown_leave {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  100% {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }
}

@keyframes __x_popup_slidedown_leave {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  100% {
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }
}