.x-button {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 100%;
  height: 0.9rem;
  font-size: 0.3rem;
  border-radius: 0.05rem;
  white-space: nowrap;
  -webkit-transition: all 0.15s ease-out;
  transition: all 0.15s ease-out;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.x-button:active {
  opacity: 1;
}

.x-button .x-button__loading {
  width: 0.25rem;
  height: 0.25rem;
  margin-right: 0.1rem;
}

.x-button .x-button__loading .x-loading__spin_path {
  stroke: #fff;
  stroke-width: 5px;
}

.x-button--default {
  background: #f0f0f0;
  color: #aaaaaa;
}

.x-button--default:active {
  background: #e6e6e6;
  color: #a5a5a5;
  opacity: 1;
}

.x-button--default .x-loading__spin_path {
  stroke: #aaaaaa !important;
}

.x-button--default.x-button--hollow {
  border: 1px solid #ededed;
  background: #fff;
}

.x-button--default.x-button--hollow:active {
  border: 1px solid #dadada;
  background: whitesmoke;
}

.x-button--primary {
  background: #00bb55;
  color: #fff;
}

.x-button--primary:active {
  background: #00ac4e;
}

.x-button--primary.x-button--hollow {
  border: 1px solid #00bb55;
  background: #fff;
  color: #00bb55;
}

.x-button--primary.x-button--hollow:active {
  border: 1px solid #00ac4e;
  color: #00ac4e;
  background: rgba(0, 172, 78, 0.1);
}

.x-button--primary.x-button--hollow .x-loading__spin_path {
  stroke: #00bb55;
}

.x-button--danger {
  background: #ff5f4a;
  color: #fff;
}

.x-button--danger:active {
  background: #eb5744;
}

.x-button--danger.x-button--hollow {
  border: 1px solid #ff5f4a;
  background: #fff;
  color: #ff5f4a;
}

.x-button--danger.x-button--hollow:active {
  border: 1px solid #eb5744;
  color: #eb5744;
  background: rgba(235, 87, 68, 0.1);
}

.x-button--danger.x-button--hollow .x-loading__spin_path {
  stroke: #ff5f4a;
}

.x-button--mini {
  width: auto;
  display: inline-flex;
  height: 0.7rem;
  font-size: 0.28rem;
  padding: 0 0.3rem;
}

.x-button--mini .x-button__loading .x-loading__spin {
  width: 0.2rem;
  height: 0.2rem;
}

.x-button--disabled {
  opacity: 0.6;
  pointer-events: none;
  cursor: not-allowed;
}