.x-space {
  display: inline-block;
  min-height: 1px;
  height: inherit;
  visibility: hidden;
}

.x-space--1 {
  width: 0.1rem !important;
  flex: 0 1 0.1rem !important;
}

.x-space--2 {
  width: 0.2rem !important;
  flex: 0 1 0.2rem !important;
}

.x-space--3 {
  width: 0.3rem !important;
  flex: 0 1 0.3rem !important;
}

.x-space--4 {
  width: 0.4rem !important;
  flex: 0 1 0.4rem !important;
}

.x-space--5 {
  width: 0.5rem !important;
  flex: 0 1 0.5rem !important;
}