.x-input {
  display: flex;
  height: 0.9rem;
  width: 100%;
}

.x-input--mini {
  height: 0.7rem;
}

.x-input--multi {
  height: 1.75rem;
}

.x-input .x-input__ipt {
  font-size: 0.3rem;
  display: block;
  flex: 1 1;
  width: 100%;
  height: 100%;
  line-height: 1.5;
  color: #333;
  padding: 0.2rem;
  border: 1px solid #ededed;
  border-radius: 0.05rem;
  background: #fff;
  font-weight: normal;
  -webkit-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
}

.x-input .x-input__ipt::-webkit-input-placeholder {
  color: #b4b4b4;
}

.x-input .x-input__ipt:not(:last-child) {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.x-input .x-input__ipt:not(:first-child) {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.x-input .x-input__ipt:focus {
  border: 1px solid #00bb55;
  position: relative;
  z-index: 2;
  box-shadow: 0 0 0.1rem rgba(0, 187, 85, 0.3);
}

.x-input--disabled .x-input__ipt {
  color: #b4b4b4 !important;
}

.x-input.x-input--error .x-input__ipt {
  border: 1px solid #ff5f4a;
  box-shadow: 0 0 0.1rem rgba(255, 95, 74, 0.3);
  color: #ff5f4a !important;
}

.x-input__addon-before,
.x-input__addon-after {
  display: flex;
  align-items: center;
  height: 100%;
  background: #fff;
  border: 1px solid #ededed;
  border-radius: 0.05rem;
  font-size: 0.3rem;
}

.x-input__addon-before > *,
.x-input__addon-after > * {
  padding: 0 0.2rem;
  height: 100%;
  display: flex;
  align-items: center;
}

.x-input__addon-before {
  border-right: none;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.x-input__addon-after {
  border-left: none;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.x-input [type='date']::-webkit-clear-button {
  display: none;
}

.x-input [type='date']::-webkit-inner-spin-button {
  display: none;
}

.x-input [type='date']::-webkit-datetime-edit-text {
  color: #ddd;
}

.x-input [type='date']::-webkit-calendar-picker-indicator {
  background: rgba(0, 0, 0, 0);
  color: #ff5f4a;
  height: 0.5em;
}