.x-loading {
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  z-index: 99995;
  background: rgba(255, 255, 255, 0.5);
}

.x-loading .x-loading__spin {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 0.6rem;
  height: 0.6rem;
  margin: -0.3rem 0 0 -0.3rem;
}

.x-loading .x-loading__spin_path {
  stroke-width: 2px;
}

.x-loading .x-loading__inner {
  opacity: 0;
  -webkit-transition: opacity 0.2s ease-out;
  transition: opacity 0.2s ease-out;
  padding-top: 1rem;
}

.x-loading .x-loading__inner p {
  font-size: 0.24rem;
  color: #00bb55;
}

.x-loading--show .x-loading__inner {
  opacity: 1;
}