.x-alert {
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  z-index: 99996;
  background: rgba(0, 34, 54, 0.2);
  opacity: 0;
  -webkit-transition: opacity 0.2s ease-out;
  transition: opacity 0.2s ease-out;
}

.x-alert__inner {
  width: 5.8rem;
  background: #fff;
  box-shadow: 0 0 0.16rem rgba(0, 39, 61, 0.05);
  border-radius: 0.08rem;
  opacity: 0;
  -webkit-transform-origin: 50%;
  transform-origin: 50%;
}

.x-alert__body {
  padding: 0.6rem 0.4rem 0.2rem 0.4rem;
}

.x-alert__body > h1 {
  display: block;
  margin-bottom: 0.15rem;
  font-size: 0.34rem;
  line-height: 1.5;
  margin-top: -0.1rem;
  color: #333;
}

.x-alert__body > .desc {
  font-size: 0.28rem;
  line-height: 1.5;
  color: #999;
}

.x-alert__body > .desc.no-title {
  color: #333;
}

.x-alert__btns {
  display: flex;
  padding: 0.4rem;
  justify-content: flex-end;
}

.x-alert__btns a {
  font-size: 0.28rem;
}

.x-alert__btns a:not(:last-child) {
  margin-right: 0.2rem;
}

.x-alert--show {
  opacity: 1;
}

.x-alert--show .x-alert__inner {
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
  opacity: 1;
  -webkit-animation: __x_alert_show 0.2s ease-out;
  animation: __x_alert_show 0.2s ease-out;
}

@-webkit-keyframes __x_alert_show {
  0% {
    -webkit-transform: scale(1.4, 1.4);
    transform: scale(1.4, 1.4);
    opacity: 0;
  }

  75% {
    -webkit-transform: scale(0.95, 0.95);
    transform: scale(0.95, 0.95);
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    opacity: 1;
  }
}

@keyframes __x_alert_show {
  0% {
    -webkit-transform: scale(1.4, 1.4);
    transform: scale(1.4, 1.4);
    opacity: 0;
  }

  75% {
    -webkit-transform: scale(0.95, 0.95);
    transform: scale(0.95, 0.95);
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    opacity: 1;
  }
}

.x-alert--hide {
  opacity: 0;
  -webkit-transition: opacity 0.15s ease-out;
  transition: opacity 0.15s ease-out;
  pointer-events: none;
}

.x-alert--hide .x-alert__inner {
  -webkit-transform: scale(0.95, 0.95);
  transform: scale(0.95, 0.95);
  -webkit-transition: all 0.15s ease-out;
  transition: all 0.15s ease-out;
}