.x-spin {
  display: flex;
  align-items: center;
  justify-content: center;
}

.x-spin__text {
  color: #00bb55;
  font-size: 0.28rem;
  margin-left: 0.1rem;
}

.x-loading__spin {
  width: 0.3rem;
  height: 0.3rem;
  overflow: hidden;
  position: relative;
}

.x-loading__spin_circular {
  -webkit-animation: __x_loading_rotate 2s linear infinite;
  animation: __x_loading_rotate 2s linear infinite;
  height: 100%;
  -webkit-transform-origin: center center;
  transform-origin: center center;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.x-loading__spin_path {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  stroke: #00bb55;
  stroke-width: 4px;
  -webkit-animation: __x_loading_dash 1.5s ease-in-out infinite;
  animation: __x_loading_dash 1.5s ease-in-out infinite;
  stroke-linecap: round;
}

@-webkit-keyframes __x_loading_rotate {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes __x_loading_rotate {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-webkit-keyframes __x_loading_dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px;
  }

  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px;
  }
}

@keyframes __x_loading_dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px;
  }

  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px;
  }
}