.x-modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99990;
}

.x-modal__mask {
  background: rgba(0, 34, 54, 0.2);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-transition: opacity 0.2s ease-out !important;
  transition: opacity 0.2s ease-out !important;
}

.x-modal__inner {
  width: 100%;
  background: #fafafa;
  box-shadow: 0 0 0.16rem rgba(0, 39, 61, 0.05);
  position: relative;
  z-index: 2;
}

.x-modal.x-modal--init {
  opacity: 0;
}

.x-modal.x-modal--enter .x-modal__mask {
  opacity: 1 !important;
}

.x-modal.x-modal--leave .x-modal__mask {
  opacity: 0 !important;
}