.auto-ui_navbar {
  height: 1.1rem;
  line-height: 1.1rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: #fff;
  position: relative;
  display: flex;
  align-items: center;
  box-shadow: 0 0.2rem 0.2rem -0.2rem rgba(0, 0, 0, 0.08);
  font-size: 0.34rem;
  color: #333;
  z-index: 2;
}

.auto-ui_navbar-icon {
  height: 1.1rem;
  width: 0.66rem;
  display: inline-block;
  background-size: 0.33rem 0.37rem;
  background-repeat: no-repeat;
  background-position: 0 50%;
}

.auto-ui_navbar-leftbtn {
  position: absolute;
  top: 0;
  left: 0;
  padding-left: 0.4rem;
  height: 100%;
  display: flex;
  align-items: center;
}

.auto-ui_navbar-leftbtn-icon {
  background-image: url(data:image/png;base64,);
  background-size: 0.44rem;
}

.auto-ui_navbar-title {
  width: 100%;
  text-align: center;
  padding: 0 1.18rem;
  font-weight: 600;
  font-size: 0.34rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.auto-ui_navbar-rightbtn {
  position: absolute;
  top: 0;
  right: 0;
  padding-right: 0.4rem;
  height: 100%;
  display: flex;
  align-items: center;
}

.auto-ui_navbar-rightbtn-icon {
  background-image: url("data:image/png;base64,");
  background-position: 99% 50%;
}

.auto-ui_navbar-dark {
  background-color: #303439;
  color: #fff;
}

.auto-ui_navbar-dark .auto-ui_navbar-leftbtn-icon {
  background-image: url(data:image/png;base64,);
}

.auto-ui_navbar-dark .auto-ui_navbar-rightbtn-icon {
  background-image: url("data:image/png;base64,");
}

.auto-ui_navbar-transparent {
  background-color: transparent;
  box-shadow: none;
}

.auto-ui_navbar-transparent .auto-ui_navbar-rightbtn-icon {
  background-image: url("data:image/png;base64,");
  width: 0.66rem;
  height: 0.66rem;
  background-color: rgba(51, 51, 51, 0.5);
  border-radius: 50%;
  background-position: center;
}