.x-switch {
  display: inline-flex;
  width: 1rem;
  height: 0.54rem;
  background-color: gainsboro;
  border-radius: 0.27rem;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  position: relative;
  box-shadow: 0 0 0 0 rgba(0, 187, 85, 0) inset;
}

.x-switch:active {
  opacity: 1;
  background-color: #d2d2d2;
}

.x-switch > em {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 0.46rem;
  height: 0.46rem;
  position: absolute;
  left: 0.04rem;
  top: 0.04rem;
  background-color: #fff;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.x-switch > em svg {
  width: 0.22rem;
  height: 0.22rem;
}

.x-switch > em path {
  fill: #ddd;
}

.x-switch.x-switch--active path {
  fill: #00bb55;
}

.x-switch--active {
  box-shadow: 0 0 0 0.3rem #00bb55 inset;
  background-color: #00bb55;
}

.x-switch--active em {
  -webkit-transform: translate(0.46rem, 0);
  transform: translate(0.46rem, 0);
}

.x-switch--active:active {
  box-shadow: 0 0 0 0.3rem #00ac4e inset;
}

.x-switch--disabled {
  pointer-events: none;
  opacity: 0.5;
}

.x-switch--io {
  width: 1.2rem;
}

.x-switch--io sub,
.x-switch--io sup {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  font-style: normal;
  color: #fff;
  padding: 0 0.2rem;
  font-size: 0.24rem;
  -webkit-transition: opacity 0.2s ease-out;
  transition: opacity 0.2s ease-out;
  opacity: 0;
}

.x-switch--io sub {
  justify-content: flex-end;
  opacity: 1;
}

.x-switch--io.x-switch--active sub {
  opacity: 0;
}

.x-switch--io.x-switch--active sup {
  opacity: 1;
}

.x-switch--io.x-switch--active em {
  -webkit-transform: translate(0.66rem, 0);
  transform: translate(0.66rem, 0);
}