.x-tabs {
  display: flex;
  height: 1rem;
  position: relative;
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #ededed;
}

.x-tabs__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  flex: 1 1;
  background: #fff;
  font-size: 0.3rem;
  color: #999;
  position: relative;
}

.x-tabs__item:active {
  color: inherit;
}

.x-tabs--active {
  color: #333;
}

.x-tabs--active:active {
  color: #333;
  opacity: 1;
}

.x-tabs__line {
  width: 50%;
  height: 0.06rem;
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(0.86, 0, 0.07, 1);
  transition: -webkit-transform 0.3s cubic-bezier(0.86, 0, 0.07, 1);
  transition: transform 0.3s cubic-bezier(0.86, 0, 0.07, 1);
  transition: transform 0.3s cubic-bezier(0.86, 0, 0.07, 1), -webkit-transform 0.3s cubic-bezier(0.86, 0, 0.07, 1);
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 9;
  top: auto;
  pointer-events: none;
}

.x-tabs__line:after {
  content: '';
  background: #00bb55;
  width: 0.3rem;
  height: 0.06rem;
  position: absolute;
  left: 50%;
  top: 0;
  margin-left: -0.15rem;
  border-radius: 0.03rem;
}