.x-cell {
  border-top: 1px solid #ededed;
  border-bottom: 1px solid #ededed;
  background: #fff;
  overflow: hidden;
  min-height: 1rem;
}

.x-cell__row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 0.2rem 0.4rem;
  min-height: 1rem;
  font-size: 0.3rem;
  color: #333;
  position: relative;
  border-bottom: 1px solid #ededed;
}

.x-cell__row > label {
  font-size: 0.3rem;
}

.x-cell__row:last-child {
  border-bottom: none;
}

.x-cell__row--arrow {
  padding-right: 0.6rem;
}

.x-cell__row--arrow::before {
  content: '';
  display: block;
  width: 0.2rem;
  height: 0.2rem;
  position: absolute;
  pointer-events: none;
  right: 0.25rem;
  top: 50%;
  background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAUBAMAAAB/pwA+AAAAElBMVEUAAAAAAAAAAAAAAAAAAAAAAADgKxmiAAAABnRSTlMAMx4BGSyQ+0qnAAAAOElEQVQI12NAB47KcGagkAFcVBAuzCSIEFYkRhjGZA4UhDGN4KLMioIKxAkyIAQZEIIMjjBBTAAAugMFyk8j/LAAAAAASUVORK5CYII=");
  background-size: 100% 100%;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
}

.x-cell__row--activeable:active {
  opacity: 1;
  background: #fafafa;
}

.x-cell--indent-line .x-cell__row {
  border-bottom: none;
}

.x-cell--indent-line .x-cell__row::after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background: #ededed;
  position: absolute;
  left: 0.4rem;
  right: 0;
  bottom: 0;
}

.x-cell--indent-line .x-cell__row:last-child:after {
  display: none;
}

.x-cell__title {
  display: flex;
  align-items: center;
  margin-top: 0.3rem;
}

.x-cell__title > h2 {
  font-size: 0.3rem;
  font-weight: normal;
  color: #666;
  line-height: 0.6rem;
}