.x-time-picker {
  background: #fff;
  display: flex;
  height: 3.9rem;
  overflow: hidden;
  padding: 0.2rem 0.6rem;
  flex-wrap: nowrap;
  position: relative;
}

.x-time-picker > div ul {
  padding: 1.4rem 0;
  overflow: hidden;
  touch-action: none;
}

.x-time-picker > div ul li {
  height: 0.7rem;
  line-height: 0.7rem;
  text-align: center;
  font-size: 0.36rem;
  color: #333;
  display: block;
}

.x-time-picker__mask-t,
.x-time-picker__mask-b {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 1.6rem;
  background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(#fff), to(rgba(255, 255, 255, 0.7)));
  pointer-events: none;
  z-index: 9;
}

.x-time-picker__mask-b {
  top: auto;
  bottom: 0;
  background-image: -webkit-gradient(linear, 0 bottom, 0 0, from(#fff), to(rgba(255, 255, 255, 0.7)));
}

.x-time-picker__day-list {
  flex-grow: 1;
}

.x-time-picker__hh-list {
  flex-grow: 1;
}

.x-time-picker__mm-list {
  flex-grow: 1;
}